@font-face {
	font-family: "TechnicLite";
	src: url(./Resources/TechnicLite.ttf);
}

html {
	height: 100%;
}

body {
	margin: 0;
	height: 100%;
	background-color: #66cccc;
}

#root {
	min-height: 100%;
}

.farmer-main {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	overflow: auto;
	overflow-x: hidden;
}

.parent {
	display: flex;
	flex-direction: column;
	width: 80%;
	margin-top: 5%;
	background-color: #f4f3f3;
	font-family: TechnicLite;
}

.body-with-picture-container {
	background-image: url(./Resources/img2.jpg);
	background-size: cover;
	height: 250px;
	width: 100%;
	display: flex;
	flex-direction: column-reverse;
}

.menu-container {
	background-color: rgb(255, 255, 255, 0.7);
	height: 20%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.menu {
	overflow: hidden;
	margin-left: 30%;
	display: flex;
	justify-content: space-around;
	font-size: x-large;
	align-items: center;
	height: 100%;
	width: 90%;
}

.menu-hidden {
	overflow: hidden;
	transition: 0.5s;
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 100%;
	font-size: small;
	margin: 0;
	margin-right: -100%;
}

.menu div {
	text-align: center;
	cursor: pointer;
}

.menu div a {
	display: inline-block;
	line-height: 1;
	text-decoration: none;
	cursor: pointer;
	position: relative;
}

.menu div a:after {
	display: block;
	content: "";
	background-color: #07aa19;
	height: 3px;
	width: 0%;
	left: 50%;
	position: absolute;
	-webkit-transition: width 0.3s ease-in-out;
	-moz--transition: width 0.3s ease-in-out;
	transition: width 0.3s ease-in-out;
	-webkit-transform: translateX(-50%);
	-moz-transform: translateX(-50%);
	transform: translateX(-50%);
}

.menu div a:hover:after,
.menu div a:focus:after {
	width: 100%;
}

.menu-checked {
	margin-top: 3px;
	border-bottom: #07aa19 3px solid;
}

.avatar-image-container {
	position: absolute;
	border: 0.8vh solid #f4f3f3;
	top: 180px;
	left: 15%;
	border-radius: 50%;
	overflow: hidden;
}

.avatar-image {
	border-radius: 50%;
	height: 220px;
	width: 220px;
	background-position: center;
	background-size: cover;
	display: flex;
	align-items: center;
	justify-content: center;
}

