.avatar-image-container:hover .avatar-image:after,
.avatar-image-container:focus .avatar-image:after {
	animation: appearAnimBackground 0.5s;
	display: block;
	top: 50%;
}

@keyframes appearAnimText {
	0% {
		margin-top: 150%;
	}

	100% {
		margin-top: 50%;
	}
}

@keyframes disappearBurgerMenu {
	0% {
		margin-right: 0%;
	}

	100% {
		margin-right: -150%;
	}
}

@keyframes appearBurgerMenu {
	0% {
		margin-right: -100%;
	}

	100% {
		margin-right: 0%;
	}
}

.show-edit-image-block {
	font-family: "Verdana";
	background-color: rgba(21, 26, 30, 0.857);
	border-bottom-left-radius: 175px;
	border-bottom-right-radius: 175px;
	width: 100%;
	display: flex;
	justify-content: space-around;
	flex-direction: column;
	height: 50%;
	margin-top: 50%;
	animation: appearAnimText 0.2s;
}

.hide-edit-image-block {
	font-family: "Verdana";
	background-color: rgba(21, 26, 30, 0.857);
	border-bottom-left-radius: 175px;
	border-bottom-right-radius: 175px;
	width: 100%;
	display: flex;
	justify-content: space-around;
	flex-direction: column;
	height: 50%;
	margin-top: 150%;
	transition: 0.2s;
}

.farmer-profile-container {
	display: flex;
	flex-direction: column;
}

.information-container {
	margin-left: 20vw;
	display: flex;
	background-color: #f4f3f3;
}

.name-of-the-farmer {
	font-size: larger;
	font-weight: 400;
	text-align: center;
	font-family: "Verdana";
}

.farmers-address-container {
	margin-left: 3vw;
	font-weight: 200;
	font-size: larger;
	font-family: "Verdana";
}

.farmers-address-container img {
	width: 3vw;
	height: auto;
}

.achievements-container {
	display: flex;
}

.progress-container {
	display: flex;
	justify-content: space-between;
	margin-left: 5%;
	width: 25%;
}

.progress {
	background-color: #33cc99;
	height: 100%;
	width: 90%;
	border-radius: 10px;
	overflow: hidden;
}

.value-progress {
	margin-left: 1vw;
	font-size: medium;
	align-items: center;
}

.followers-and-following-container {
	display: flex;
}

.followers {
	margin-left: 4.5vw;
	display: flex;
	font-size: medium;
	flex-direction: column;
}

.following {
	margin-left: 5.5vw;
	display: flex;
	font-size: medium;
	flex-direction: column;
}

.follow-count {
	margin-bottom: 0;
	text-align: center;
}

.follow-style {
	margin-top: 0;
}

.edit-image-block,
.show-image-block {
	text-align: center;
	color: #f4f3f3;
	height: 13px;
	font-size: 13px;
	cursor: pointer;
}

.edit-image-block:hover,
.show-image-block:hover {
	color: #ffffff;
}

.edit-image-block-icon,
.show-image-block-icon {
	fill: white;
	align-self: center;
}

.about-fermer-container {
	display: flex;
	justify-content: center;
	margin-left: 20%;
	margin-right: 20%;
	flex-direction: column;
	margin-bottom: 5%;
	margin-top: 5%;
}

.occupation-container {
	width: 80%;
	display: flex;
	border-bottom: 2px solid #cccc;
	justify-content: center;
	font-weight: bolder;
	font-size: large;
	padding-bottom: 2vh;
}

.occcupation {
	margin-left: 2vh;
}

.about-fermer {
	margin-top: 2vh;
	display: flex;
	justify-content: center;
}

.text-about-fermer {
	width: 90vh;
	font-size: large;
}

.border {
	width: 80%;
	align-self: center;
	border-bottom: 2px solid #cccc;
	margin-bottom: 25px;
}

.darken-background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgb(0, 0, 0, 0.6);
	z-index: 5;
	display: flex;
	justify-content: center;
}

#menu__toggle {
	display: none;
}

#menu__toggle:checked ~ .menu__btn > span {
	transform: rotate(45deg);
}
#menu__toggle:checked ~ .menu__btn > span::before {
	top: 0;
	transform: rotate(0);
}
#menu__toggle:checked ~ .menu__btn > span::after {
	top: 0;
	transform: rotate(90deg);
}

/* #menu__toggle:checked ~ .menu {
	visibility: visible;
	left: 0;
} */

.burger-container {
	margin-right: 10px;
}

.menu__btn {
	display: flex;
	align-items: center;
	width: 20px;
	height: 20px;

	cursor: pointer;
	z-index: 1;
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
	display: block;
	position: absolute;

	width: inherit;
	height: 2px;

	background-color: #616161;

	transition-duration: 0.25s;
}
.menu__btn > span::before {
	content: "";
	top: -8px;
}
.menu__btn > span::after {
	content: "";
	top: 8px;
}

